          {
            code: "board",
            name: "TABLEROS",
            template: [
              //Plantilla  de los datos
            ],
            data: [
              /*   {
                  reference: 1,
                  nomenclature: "new tablero",
                  location: "palapa",
                  items: [1, 2, 1, 2, 1, { name: "hola" }],
                }, */
            ],
          },
        ],
      };
      this.$store.dispatch("updateClientAction", dt);
    },
    start() {
      const data = this.$store.getters.getClient;
      console.log(data);
      this.id = data.id;
      this.tradename = data.tradename;
      this.municipality = data.municipality;
      this.newDate = data.date;
      this.whoAttendedUs = data.whoAttendedUs;
      this.typeOfOpinion = data.typeOfOpinion;
    },
  },
};
</script>